.Register {
  display: flex;
  box-sizing: content-box;
  flex-direction: column;
  align-items: center;
  width: 50%;
}

.Register16bit {
  width: 100%;
}

.Register>div:first-child {
  width: 100%;
  text-align: center;
  background-color: #42a5f5;
  font-style: normal;
  font-weight: bolder;
  font-size: 1.2rem;
  padding: 15px;
  border-top: 10px solid white;
  border-left: 5px solid white;
  border-right: 5px solid white;
}

.Register>div:nth-child(2) {
  width: 100%;
  text-align: center;
  background-color: rgb(216, 229, 231);
  padding: 15px;
  border-left: 5px solid white;
  font-style: italic;
  font-size: 1rem;
  border-right: 5px solid white;
}

.RegisterPair {
  display: flex;
  border-left: 1px solid black;

}

@media only screen and (min-width: 768px) {
  .Register>div:nth-child(2) {
    padding: 6px;
    font-size: 1rem;
  }

  .Register>div:first-child {
    font-weight: bolder;
    font-size: 1.2rem;
    padding: 6px;
  }
}